export default {
  name: 'ItemPreview',
  props: {
    preview: {
      type: Object,
      required: true
    },
    selected: {
      type: String,
      default: null
    }
  },
  computed: {
    checked: {
      get() {
        return this.selected === this.preview.id;
      },

      set(checked) {
        this.$emit('select', checked ? this.preview.id : '');
      }

    }
  }
};